.header {
  & .container {
    position: relative;
  }

  //.header__container
  &__container {
    position: absolute;
    display: flex;
    justify-content: space-between;
    z-index: 2;

    @media screen and (max-width: $tablet - 1px) {
      top: 40px;
      left: 31px;
      width: 87%;
      align-items: center;
    }

    @media screen and (min-width: $tablet) {
      width: 89.8%;
      top: 32px;
      left: 39px;
      align-items: flex-start;
    }

    @media screen and (min-width: $desktop) {
      left: 15px;
      width: 97.5%;
      align-items: center;
      justify-content: flex-start;
    }
  }

  //   // .header__menu-button
  &__menu-button {
    display: inline-flex;
    color: $primary-white-color;
    z-index: 2;

    &:focus,
    &:hover {
      color: $accent-color;
    }

    &.is-open {
      position: fixed;
      top: 52px;
      right: 20px;
      color: $primary-text-color;

      &:focus,
      &:hover {
        color: $accent-color;
      }
    }

    @media screen and (min-width: $tablet) {
      display: none;
    }
  }

  //.header__menu-backdrop
  &__menu-backdrop {
    @media screen and (max-width: $tablet - 1px) {
      position: fixed;
      top: 0;
      left: 0;
      width: 100vw;
      height: 100%;

      background-color: rgba(25, 28, 38, 0.2);
      box-shadow: -1px 0px 3px rgba(#000, 0.16), -2px 0px 4px rgba(#000, 0.08), -4px 0px 8px rgba(#000, 0.04);

      opacity: 0;
      pointer-events: none;
      overflow: hidden;
      transition: opacity $timming;
    }

    //.header__menu-backdrop.is-open
    &.is-open {
      pointer-events: auto;
      opacity: 1;

      & > .header__menu-container {
        transform: translateX(0);
      }
    }
  }

  //.header__menu-container
  &__menu-container {
    font-weight: 600;
    line-height: 1.4;
    letter-spacing: 0.1em;

    @media screen and (max-width: $tablet - 1px) {
      position: absolute;
      top: 0;
      right: 0;
      width: 70vw;
      height: 100%;

      padding: 118px 0 0 40px;
      background-color: $primary-white-color;
      box-shadow: -1px 0px 3px rgba(#000, 0.16), -2px 0px 4px rgba(#000, 0.08), -4px 0px 8px rgba(#000, 0.04);

      transform: translateX(100%);
      transition: transform $timming;
      overflow-y: scroll;
    }

    @media screen and (min-width: $tablet) {
      display: flex;
      flex-direction: row;
      margin-top: 8px;
    }

    @media screen and (min-width: $desktop) {
      align-items: center;
      margin-top: 0;
    }
  }

  //.header__nav
  &__nav {
  }

  //.header__nav-list
  &__nav-list {
    @media screen and (max-width: $tablet - 1px) {
      margin-bottom: 40px;
      max-width: 155px;
    }

    @media screen and (min-width: $tablet) {
      display: flex;
      margin-top: 12px;
      margin-right: 47px;
    }

    @media screen and (min-width: $desktop) {
      align-items: center;
      margin-top: 0;
      margin-right: 317px;
    }
  }

  //.header__nav-item
  &__nav-item:not(:last-child) {
    @media screen and (min-width: $tablet) {
      margin-right: 24px;
    }

    @media screen and (min-width: $desktop) {
      margin-right: 40px;
    }
  }

  //.header__nav-link
  &__nav-link {
    display: inline-block;
    padding: 12px 0;
    transition: color $timming;

    @media screen and (min-width: $tablet) {
      font-size: 12px;
      color: $primary-white-color;
    }
    &:focus,
    &:hover {
      color: $accent-color;
    }
  }

  //.header__contacts
  &__contacts {
    display: flex;

    @media screen and (max-width: $tablet - 1px) {
      flex-direction: column;
      margin-bottom: 52px;
      max-width: 194px;
    }

    @media screen and (min-width: $tablet) and (max-width: $desktop - 1px) {
      flex-direction: column-reverse;
      align-items: center;
    }

    @media screen and (min-width: $desktop) {
      margin-top: 8px;
    }
  }

  //.header__contacts-link
  &__contacts-link {
    padding: 12px 0;

    font-size: 12px;
    color: $primary-white-color;
    transition: color $timming;

    @media screen and (max-width: $tablet - 1px) {
      margin-bottom: 12px;
      font-size: 18px;
      color: $primary-text-color;
    }

    @media screen and (min-width: $desktop) {
      margin-right: 40px;
    }

    &:hover,
    &:focus {
      color: $accent-color;
    }
  }

  //.header__button
  &__button {
    text-align: center;
    @media screen and (max-width: $tablet - 1px) {
      color: $primary-text-color;
      transition: color $timming, background-color $timming;

      &:focus,
      &:hover {
        color: $primary-white-color;
      }
    }
  }

  //.header__social
  &__social {
    @media screen and (max-width: $tablet - 1px) {
      position: absolute;
      left: 12%;
      display: flex;
      flex-direction: column;
      padding-left: 100px;
      max-width: 194px;
    }

    @media screen and (min-width: $tablet) {
      position: absolute;
      top: 460px;
      left: 64px;
      transform-origin: bottom left;
      transform: rotate(-90deg);
    }

    @media screen and (min-width: $desktop) {
      left: 48px;
    }

    &::before {
      content: '';
      display: inline-block;
      width: 60px;
      height: 1px;
      background-color: $primary-text-color;
      position: absolute;
      top: 21px;
      left: 0;

      @media screen and (min-width: $tablet) {
        left: -100px;
      }
    }
  }

  //.header__social-link
  &__social-link {
    display: inline-block;
    padding-top: 12px;
    padding-bottom: 12px;

    font-size: 14px;
    line-height: 1.35;
    letter-spacing: 0.2em;
    color: $primary-text-color;
    transition: color $timming;

    @media screen and (min-width: $tablet) {
      margin-right: 40px;
      font-size: 11px;
    }

    &:hover,
    &:focus {
      color: $accent-color;
    }
  }
}

// mob menu icons
.icon-switch {
  fill: currentColor;

  //.icon-switch__close
  &__close {
    display: none;
  }

  .header__menu-button.is-open &__close {
    display: block;
  }

  //.icon-switch__menu
  .header__menu-button.is-open &__menu {
    display: none;
  }
}

.logo {
  @media screen and (min-width: $tablet) and (max-width: $desktop - 1px) {
    margin-left: 11px;
  }

  @media screen and (min-width: $desktop) {
    margin-right: 71px;
  }
  //.logo__icon
  &__icon {
    @media screen and (max-width: $tablet - 1px) {
      fill: $primary-white-color;
    }
  }
}
