.hero {
  position: relative;
  max-width: $mobile;
  margin: 0px auto;
  overflow: hidden;

  @media screen and (min-width: $tablet) {
    max-width: $tablet;
  }

  @media screen and (min-width: $desktop) {
    max-width: $desktop;
  }

  //.hero__container
  &__container {
    @media screen and (min-width: $desktop) and (max-width: 1279px) {
      position: relative;
      left: 57px;
    }
  }
  //.hero__img
  &__img {
    margin-left: auto;
  }

  //.hero__content
  &__content {
    position: absolute;
    top: 43.3vw;
    left: 31px;
    z-index: 1;

    max-width: 306px;

    @media screen and (min-width: $mobile) {
      top: 208px;
    }

    @media screen and (min-width: $tablet) {
      top: 272px;
      left: 279px;

      max-width: 355px;
    }

    @media screen and (min-width: $desktop) {
      left: 170px;

      max-width: 450px;
    }

    @media screen and (min-width: 1280px) {
      left: 255px;
    }
  }

  //.hero__description
  &__description {
    margin: 0px 0px 20px;
    padding: 0px;

    font-weight: 600;
    font-size: 2.3vw;
    line-height: 1.36;
    letter-spacing: 0.1em;
    text-transform: uppercase;
    color: rgba($secondary-text-color, 0.6);

    &::before {
      content: '';
      display: inline-block;
      width: 60px;
      height: 1px;
      color: inherit;
      margin: 0px 20px 4px 0px;
    }

    @media screen and (min-width: $mobile) {
      font-size: 11px;
    }
  }

  //.hero__title
  &__title {
    margin: 0px 0px 20px;
    padding: 0px;

    font-size: 10vw;
    color: $primary-white-color;

    @media screen and (min-width: $mobile) {
      font-size: 48px;
    }

    @media screen and (min-width: $desktop) {
      font-size: 72px;
      line-height: 1.18;
    }
  }

  //.hero__text
  &__text {
    margin: 0px;
    padding: 0px;

    font-size: 3.33vw;
    line-height: 1.63;
    color: $secondary-text-color;

    @media screen and (min-width: $mobile) {
      font-size: 16px;
    }

    @media screen and (min-width: $desktop) {
      font-size: 18px;
      line-height: 1.67;
    }
  }
}
